import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Search from '../views/Search.vue'
import Searching from '../views/Searching.vue'
import SearchList from '../views/SearchList.vue'
import SearchDetails from '../views/SearchDetails.vue'
import SearchBook from '../views/SearchBook.vue'
import SearchGrabble from '../views/SearchGrabble.vue'
import ListCoding from '../views/ListCoding.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/search',
    name: 'search',
    component: Search,
    children: [
      {
        path: '/',
        component: SearchList
      },
      {
        path: 'searching',
        component: Searching
      },
      {
        path: 'searchDetails',
        component: SearchDetails
      },
      {
        path: 'searchBook',
        component: SearchBook
      },


    ]
  },
  {
    path: '/searchGrabble',
    name: 'searchGrabble',
    component: SearchGrabble
  },

  {
    path: '/listCoding',
    name: ListCoding,
    component: ListCoding
  }
]

const router = new VueRouter({
  routes
})

export default router
