<template>
  <div class="searching">
    <HomeTitle>
      <slot>搜索</slot>
    </HomeTitle>
    <div class="seek">
      <van-search v-model="inputValue" placeholder="请输入搜索关键词" show-action shape="round">
        <!-- <div class="text" slot="action" @click="$router.push('/search/相关内容')" >确定</div> -->
        <div class="text" slot="action"  @click="getTrendingSearch" >确定</div>
      </van-search>
    </div>

    <template v-if="!inputValue">
      <div class="hot">
        <h2>热门推荐</h2>
        <ul class="keywords">
          <li v-for="(item, index) in SearchWords.slice(0,8)" :key="index"  @click="inputValue=item,getTrendingSearch()">{{item}}</li>
        </ul>
      </div>
    </template>
    <template v-else>
      <dd class="thing">
        <dl v-for="(item, index) in  Correlation" :key="index" @click="inputValue=item,getTrendingSearch()">{{item}}</dl>
      </dd>
    </template>

    <template>
      <div class="history">
      
         <h2>搜索历史</h2>
         <div class="remove">
         <h5>删除历史</h5>
         <van-icon name="delete" class="cancel" size="18"/>
          </div>
      </div>
    </template>
  </div>
</template>


<script>
import HomeTitle from "@/components/HomeTitle.vue";
export default {
  // props:[value],
  name: "Searching",
  components: {
    HomeTitle
  },
  data() {
    return {
      inputValue: null,
      SearchWords: [],
      Correlation: []
    };
  },
  methods: {
    getCorrelation() {
      this.axios
        .get("http://novel.kele8.cn/auto-complete?query=" + this.inputValue)
        .then(response => {
          // window.console.log("^_^", response);
          this.Correlation = response.data.keywords;
          // this.keyword = response.data.keywords;
        });
    },
    getSearchWords() {
      this.axios.get("http://novel.kele8.cn/hot-books?").then(response => {
        // window.console.log(response);
        this.SearchWords = response.data.hotWords;
      });
    },
    getTrendingSearch() {
      this.$store.commit("setTrendingSearch", {
        value: this.inputValue
      });
      this.$router.push("/search");
    }
  },
  created() {
    this.getSearchWords();
    this.getCorrelation();
  },
  watch: {
    inputValue() {
      //  window.console.log("xxx")
      this.getCorrelation();
    }
  }
};
</script>

<style lang="less">
.searching {
  .seek {
    margin-top: 15px;
    .text {
      font-size: 18px;
      color: #6b6b6b;
    }
  }
  h2 {
    margin-top: 30px;
    margin-left: 20px;
    color: #6b6b6b;
  }
  ul {
    margin-top: 20px;
    li {
      display: inline-block;
      padding: 10px;
      border-radius: 50px;
      border: 1px solid rgba(184, 178, 178, 0.5);
      background: rgba(212, 209, 209, 0.5);
      margin-top: 10px;
      margin-left: 15px;
      color: #6b6b6b;
    }
  }
 .history{
   width: 100%;
   height: 100px;
   position: relative;

   h5{
     position: absolute;
     top: -15px;
     right: 35px;
     color: #8a8a8f;
     letter-spacing: 1px;
     
   }
   .cancel{
     position: absolute;
     top: 8px;
     right: 10px;
   }
 }
}
</style>
