<template>
  <div class="list">
    <HomeTitle>
      <slot>相关书籍</slot>
    </HomeTitle>
    <ul>
      <li v-for="(item, index) in books" :key="index" @click="details(item)">
        <div class="head">
          <img :src="changeUrl(item.cover)" alt />
        </div>

        <div class="about">
          <h5>{{item.title}}</h5>
          <p class="person">{{item.author}} | {{item.cat}}</p>
          <i>{{item.shortIntro}}</i>
          <span>
            {{translatePlayCount(item.latelyFollower)}}
            <i>人气</i>
          </span> |
          <span>
            {{translatePlayCount(item.retentionRatio)}}%
            <i>读者留存</i>
          </span>
        </div>
      </li>
    </ul>
  </div>
</template>


<script>
import HomeTitle from "@/components/HomeTitle.vue";
export default {
  name: "SearchList",
  components: {
    HomeTitle
  },

  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.axios
        .get("http://novel.kele8.cn/search", {
          params: {
            keyword: vm.$store.state.trendingSearch
          }
        })
        .then(function(response) {
          window.console.log(response);
          vm.books = response.data.books;
        });
    });
  },
  data() {
    return {
      books: []
    };
  },
  methods: {
    changeUrl(Url) {
      return unescape(Url.slice(7));
    },
    translatePlayCount(n) {
      if (n > 9999) {
        return (n / 10000).toFixed(1) + "万";
      } else {
        return n;
      }
    },

    details(item) {
      this.$router.push({
        path: "/search/SearchDetails",
        query: { id: item._id }
      });
    }
  }
};
</script>

<style lang="less">
body {
  .list {
    background: #f5f2ed;
    li {
      display: flex;
      padding: 15px;
      border-bottom: 1px solid rgba(173, 170, 170, 0.5);
      .head {
        flex: 1;
        // border:1px solid rebeccapurple;
        width: 80px;
        height: 130px;
        img {
          width: 80px;
          height: 100px;
          // border: 1px solid red;
          margin-top: 10px;
          margin-left: -5px;
        }
      }
      .about {
        flex: 3.5;
        h5 {
          margin: 3px;
        }
        .person {
          font-size: 12px;
          color: rgb(167, 163, 163);
          text-indent: 10px;
        }
        i {
          font-style: normal;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
          text-indent: 10px;
          font-size: 12px;
          color: rgb(167, 163, 163);
          margin: 3px 0 0 0;
        }
        span {
          display: inline-block;
          font-size: 14px;
          padding: 10px;
          color: #b82560;
          i {
            display: inline-block;
            line-height: 12px;
            font-size: 14px;
          }
        }
      }
    }
  }
}
</style>