import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    trendingSearch: null,
    maleActive: true,
    femaleActive: false,
    bookDetailId: null,

  },
  mutations: {
    setChangeGender(state, param) {
      state.maleActive = param.male;
      state.femaleActive = param.female;
    },
    setTrendingSearch(state, param) {
      state.trendingSearch = param.value;
    },
    setBookDetailId(state, param) {
      state.bookDetailId = param.value;
      window.console.log(state.bookDetailId);
    }
  },
  actions: {
  },
  modules: {
  }
})
