<template>
  <div class="home-search">
    <div class="all" @click="$router.push('/search/searching')">
      <van-search v-model="value" left-icon :clearable="false"></van-search>
      <!-- 关键字搜索 -->
      <van-swipe
        vertical
        :show-indicators="false"
        :autoplay="3000"
        :touchable="false"
        @change="getTrendingSearchIndex"
      >
        <van-swipe-item v-for="(item, index) in hotWords" :key="index">{{item}}</van-swipe-item>
      </van-swipe>
    </div>

    <!-- 搜索图标 -->
    <van-icon name="search" slot="right-icon" size="25" @click="getTrendingSearch" />
    <!-- 轮播 -->
    <template>
      <div class="sec">
        <van-swipe :autoplay="3000" indicator-color="white">
          <van-swipe-item>
            <img src="../img/f.jpg" alt />
          </van-swipe-item>
          <van-swipe-item>
            <img src="../img/t.jpg" alt />
          </van-swipe-item>
          <van-swipe-item>
            <img src="../img/s.jpg" alt />
          </van-swipe-item>
          <van-swipe-item>
            <img src="../img/four.jpg" alt />
          </van-swipe-item>
          <van-swipe-item>
            <img src="../img/six.jpg" alt />
          </van-swipe-item>
        </van-swipe>
      </div>
    </template>

    <!-- 最热榜、好评榜、潜力榜、留存榜、完结榜、VIP榜 -->

    <van-tabs v-model="active" sticky>
      <van-tab title="畅销榜" v-if="RankG">
        <div class="hots">
          <Headline>畅销榜</Headline>
          <ul>
            <li v-for="(item, index) in RankG" :key="index" @click="details(item)">
              <div class="rankleft">
                <img :src="changeUrl(item.cover)" alt />
              </div>
              <div class="rankright">
                <h6>{{item.title}}</h6>
                <p>{{item.shortIntro}}</p>

                <p class="texts">
                  {{item.majorCate}} |
                  <span>{{item.latelyFollower}} 人气</span>
                </p>
              </div>
            </li>
          </ul>
        </div>
      </van-tab>

      <van-tab title="VIP金榜" v-if="BooksVIP">
        <div class="hots">
          <Headline>VIP金榜</Headline>
          <ul>
            <li v-for="(item, index) in BooksVIP" :key="index" @click="details(item)">
              <div class="rankleft">
                <img :src="changeUrl(item.cover)" alt />
              </div>
              <div class="rankright">
                <h6>{{item.title}}</h6>
                <p>{{item.shortIntro}}</p>

                <p class="texts">
                  {{item.majorCate}} |
                  <span>{{item.latelyFollower}} 人气</span>
                </p>
              </div>
            </li>
          </ul>
        </div>
      </van-tab>
      <!--  -->
      <van-tab title="完结" v-if="BooksEnd">
        <div class="hots">
          <Headline>完结</Headline>
          <ul>
            <li v-for="(item, index) in BooksEnd" :key="index" @click="details(item)">
              <div class="rankleft">
                <img :src="changeUrl(item.cover)" alt />
              </div>
              <div class="rankright">
                <h6>{{item.title}}</h6>
                <p>{{item.shortIntro}}</p>

                <p class="texts">
                  {{item.majorCate}} |
                  <span>{{item.latelyFollower}} 人气</span>
                </p>
              </div>
            </li>
          </ul>
        </div>
      </van-tab>

      <van-tab title="好评榜" v-if="BooksPraise">
        <div class="hots">
          <Headline>好评榜</Headline>
          <ul>
            <li v-for="(item, index) in BooksPraise" :key="index" @click="details(item)">
              <div class="rankleft">
                <img :src="changeUrl(item.cover)" alt />
              </div>
              <div class="rankright">
                <h6>{{item.title}}</h6>
                <p>{{item.shortIntro}}</p>

                <p class="texts">
                  {{item.majorCate}} |
                  <span>{{item.latelyFollower}} 人气</span>
                </p>
              </div>
            </li>
          </ul>
        </div>
      </van-tab>

      <van-tab title="潜力榜" v-if="BooksPotential">
        <div class="hots">
          <Headline>好评榜</Headline>
          <ul>
            <li v-for="(item, index) in BooksPotential" :key="index" @click="details(item)">
              <div class="rankleft">
                <img :src="changeUrl(item.cover)" alt />
              </div>
              <div class="rankright">
                <h6>{{item.title}}</h6>
                <p>{{item.shortIntro}}</p>

                <p class="texts">
                  {{item.majorCate}} |
                  <span>{{item.latelyFollower}} 人气</span>
                </p>
              </div>
            </li>
          </ul>
        </div>
      </van-tab>
      <!--  -->
      <van-tab title="新书榜" v-if="New">
        <div class="hots">
          <Headline>新书榜</Headline>
          <ul>
            <li v-for="(item, index) in New" :key="index" @click="details(item)">
              <div class="rankleft">
                <img :src="changeUrl(item.cover)" alt />
              </div>
              <div class="rankright">
                <h6>{{item.title}}</h6>
                <p>{{item.shortIntro}}</p>

                <p class="texts">
                  {{item.majorCate}} |
                  <span>{{item.latelyFollower}} 人气</span>
                </p>
              </div>
            </li>
          </ul>
        </div>
      </van-tab>
    </van-tabs>

    <!-- <div class="some">
      <p>查看更多</p>
      <van-icon name="arrow" color="#b93321" class="run" size="18" />
    </div>-->

    <Footer />
  </div>
</template>

<script>
import Headline from "@/components/Headline.vue";
import Footer from "@/components/Footer.vue";
// import SearchDetails from '@/views/SearchDetails';
export default {
  components: {
    // HomeLei
    Headline,
    Footer
  },
  data() {
    return {
      value: "",
      active: 0,
      male: true,
      female: false,
      hotWords: [
        "超级女婿",
        "后娘[穿越]",
        "赘婿当道",
        "财阀小娇妻：谢少宠上瘾！",
        "入骨宠婚：误惹天价老公",
        "兽黑狂妃：皇叔逆天宠",
        "最强屠龙系统",
        "女神的超级赘婿",
        "剑起风云",
        "后娘最彪悍",
        "太古狂魔",
        "第一赘婿",
        "重生民国娇小姐",
        "秦夫人",
        "武将宠妻手簿",
        "御天武帝",
        "娱乐春秋",
        "阴间神探",
        "第一狂妃",
        "我在修真界当宗主",
        "本座花无缺"
      ],
      index: 0,
      RankG: null,
      RankB: null,
      New: null,
      BooksPraise: null,
      BooksPotential: null,
      BooksVIP: null,
      BooksKeep: null,
      BooksEnd: null
    };
  },
  methods: {
    changeUrl(Url) {
      return unescape(Url.slice(7));
    },
    getTrendingSearchIndex(index) {
      // this.window.console(index);
      this.index = index;
    },

    getTrendingSearch() {
      this.$store.commit("setTrendingSearch", {
        value: this.hotWords[this.index]
      });
      this.$router.push("/search");
    },
    getRank(id, male) {
      if (male) {
        this.axios.get("http://novel.kele8.cn/rank/" + id).then(response => {
          // window.console.log(response);
          ({ books: this.RankB } = response.data.ranking);
        });
      } else {
        this.axios.get("http://novel.kele8.cn/rank/" + id).then(response => {
          // window.console.log(response);
          ({ books: this.RankG } = response.data.ranking);
        });
      }
    },
    getNew() {
      this.axios
        .get("http://novel.kele8.cn/rank/5a39d453fc84c2b8ef885812")
        .then(response => {
          // window.console.log(response);
          ({ books: this.New } = response.data.ranking);
        });
    },
    getBooks(id, who) {
      this.axios.get("http://novel.kele8.cn/rank/" + id).then(response => {
        // window.console.log(response);
        // BooksPraise好评   BooksPotential本周潜力   BooksVIPVIP榜   BooksKeep留存榜 BooksEnd 追书完结

        switch (who) {
          case 0:
            ({ books: this.BooksPraise } = response.data.ranking);

            break;
          case 1:
            ({ books: this.BooksPotential } = response.data.ranking);

            break;
          case 2:
            ({ books: this.BooksVIP } = response.data.ranking);

            break;
          case 3:
            ({ books: this.BooksKeep } = response.data.ranking);

            break;

          default:
            ({ books: this.BooksEnd } = response.data.ranking);
            break;
        }
      });
    },
    details(item) {
      this.$router.push({
        path: "/search/SearchDetails",
        query: { id: item._id }
      });
      this.$store.commit("setBookDetailId", {
        value: item._id
      });
    }
  },
  created() {
    this.getRank("54d42d92321052167dfb75e3", this.male);
    this.getRank("54d43437d47d13ff21cad58b", !this.male);

    this.getNew();
    //  5a684551fc84c2b8efaab179  好评
    //  54d43709fd6ec9ae04184aa5  本周潜力
    // 57eb959df60eb7e21fb3a8b7"  VIP榜",
    //  5645482405b052fe70aeb1b5  留存榜
    //  564eb8a9cf77e9b25056162d  追书完结榜
    this.getBooks("5a684551fc84c2b8efaab179", 0);
    this.getBooks("54d43709fd6ec9ae04184aa5", 1);
    this.getBooks("57eb959df60eb7e21fb3a8b7", 2);
    this.getBooks("5645482405b052fe70aeb1b5", 3);
    this.getBooks("564eb8a9cf77e9b25056162d", 3);

    // https://novel.kele8.cn/rank/54d42d92321052167dfb75e3
    // https://novel.kele8.cn/rank/5a39d435fc84c2b8ef884798
    // 54d43437d47d13ff21cad58b
  }
};
</script>

<style lang="less">
.home-search {
  position: relative;
  .van-swipe {
    width: 50%;
    height: 30px;
    position: absolute;
    top: 10px;
    left: 20px;
    .van-swipe-item {
      line-height: 34px;
      font-size: 12px;
    }
  }
  .sec {
    position: relative;
    height: 200px;
    // border-bottom: 1px solid gray;
    .van-swipe {
      width: 100%;
      height: 200px;
      position: absolute;
      top: 0px;
      left: 0px;
      background: #b83320;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .van-icon {
    position: absolute;
    top: 15px;
    right: 20px;
  }
  .hots {
    width: 100%;
    position: relative;
    ul {
      padding: 0 15px;
      li {
        list-style: none;
        // padding: 10px;
        display: flex;
        width: 100%;
        margin-bottom: 20px;
        align-items: center;
        .rankleft {
          // flex: 1;
          width: 80px;
          height: 100px;
          // padding: 10px;
          // margin: 15px 0px;
          img {
            width: 100%;
            height: 100px;
            border: 1px solid rgb(74, 75, 77);
            // margin-top: 30px;
            // margin-left: -10px;
          }
        }
        .rankright {
          flex: 1;
          padding: 10px;
          // padding-bottom: 30px;
          // padding-top: -20px;
          border-bottom: 1px solid rgba(184, 177, 177, 0.5);

          h6 {
            // margin-left: -5px;
            // margin-right: 15px;
            // margin-bottom: 15px;
            font-size: 15px;
            font-weight: normal;
          }
          p {
            font-style: normal;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            // text-indent: 20px;
            font-size: 12px;
            color: rgb(167, 163, 163);
            // margin: 5px 0 0 0;
            line-height: 2em;
          }
          .texts {
            font-size: 14px;
            // padding-right: 15px;
            // padding-top: 10px;
            color: black;
            // margin-left: -25px;
            span {
              // padding-left: 5px;
            }
          }
        }
      }
    }
  }
  .Separator {
    width: 100%;
    height: 20px;
    position: relative;
    top: 180px;
    // border: 1px solid black;
    background: #f4f4f4;
  }
  .some {
    position: relative;
    margin-top: -5px;
    height: 250px;

    p {
      text-align: center;
      font-size: 13px;
      color: #b93321;
      line-height: 2em;
    }
    .run {
      position: absolute;
      right: 200px;
      top: 5px;
    }
  }
}
</style>