<template>
  <div class="home-nav">
    <van-nav-bar left-text="追书神器">
      <van-image
        width="100"
        height="26"
        src="data:image/png;base64,"
        slot="left"
      />
      <div class="button-group" slot="title">
        <button :class="{ active: maleActive }" @click="changeGenderBoy">男生</button>
        <button :class="{ active: femaleActive }" @click="changeGenderGirl">女生</button>
      </div>

      <van-icon name="column" slot="right" size="30" @click="$router.push('/书架')" />
      <van-icon name="user-circle-o" slot="right" size="30" @click="$router.push('/注册')" />
    </van-nav-bar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      maleActive: true,
      femaleActive: false
    };
  },
  components: {},
  methods: {
    changeGenderBoy() {
      if (this.maleActive == false && this.femaleActive == true) {
        this.maleActive = true;
        this.femaleActive = false;

        this.$store.commit("setChangeGender", {
          male: this.maleActive,
          female: this.femaleActive
        });
      } else {
        return 0;
      }
    },
    changeGenderGirl() {
      if (this.maleActive == true && this.femaleActive == false) {
        this.femaleActive = true;
        this.maleActive = false;

        this.$store.commit("setChangeGender", {
          male: this.maleActive,
          female: this.femaleActive
        });
      } else {
        return 0;
      }
    }
  }
};
</script>

<style lang="less">
.home-nav {
  .van-nav-bar {
    .van-image {
      top: 10px;
    }
    background: #b83320;
    .button-group {
      width: 100px;
      border-radius: 46px;
      margin: 0 auto;
      display: flex;
      line-height: 24px;
      padding-top: 10px;
      button {
        flex: 1;
        height: 26px;
        border: none;
        outline: none;
        border: 1px solid white;
        background: transparent;
        color: white;
        font-size: 14px;
        &:first-child {
          border-radius: 26px 0 0 26px;
        }
        &:last-child {
          border-radius: 0 26px 26px 0;
        }

        &.active {
          background: white;
          color: gray;
        }
      }
    }
    .van-icon {
      color: white;
      padding-left: 10px;
    }
  }
}
</style>