<template>
  <ul class="coding">
    <li v-for="(item, index) in Substance" :key="index">
      <h4>{{item.title}}</h4>
      <p>{{item.cpContent}}</p>
    </li>
  </ul>
</template>

<script>
export default {
  data() {
    return {
      Substance: null
    };
  },
  methods: {
    // 书籍内容
    getSubstance() {
      this.axios
        .get(
          "http://novel.kele8.cn/chapters/" +
            encodeURIComponent(this.$route.query.link)
        )
        .then(response => {
          window.console.log("sub", response);
          ({ data: this.Substance } = response);
        });
    }
  },
  created() {
    this.getSubstance();
    // window.console.log(this.$route)
  }
};
</script>

<style lang="less">

    .coding{
        background: #eee6dd;
        color:#5c5d58;
        margin: 0;
        list-style: none;
        
        h4{
            font-weight:400;
            line-height: 2em;
            margin: 0px;
        }
    }
</style>