<template>
  <div class="info" v-if="Info">
    <HomeHead>
      <slot>书籍详情</slot>
    </HomeHead>
    <!-- 作者、标签 -->
    <ul class="detail">
      <li class="detailleft" v-if="Info.cover">
        <img :src="changeUrl(Info.cover)" alt />
      </li>
      <li class="detailright">
        <h5>{{this.Info.title}}</h5>
        <div class="related">
          <span style="color:#b93321">{{this.Info.author}}</span>
          <i>|</i>
          <span>{{this.Info.cat}}</span>
          <i>|</i>
          <span>{{translatePlayCount(this.Info.wordCount)}}</span>
          <p>一小时前更新</p>
        </div>
      </li>
    </ul>
    <!-- 按钮 -->
    <section class="btn">
      <van-button class="add">加入书架</van-button>
      <van-button @click="searchGrabble(Sol,Info.title)" class="action">开始阅读</van-button>
    </section>

    <!-- 留存率 -->
    <div class="popularity">
      <div class="chase">
        <span>追人气</span>
        <p>{{moods(this.Info.latelyFollower)}}</p>
      </div>
      <div class="reader">
        <span>读者留存率</span>
        <P>{{this.Info.retentionRatio}}%</P>
      </div>
      <div class="update">
        <span>日更字数/天</span>
        <p>{{this.Info.serializeWordCount}}</p>
      </div>
    </div>

    <!-- 简介 -->
    <section class="longIntro" @click="iffull=!iffull" v-if="Info">
      <b :class="{'iffull':iffull}">{{this.Info.longIntro}}</b>
      <van-icon name="arrow-down" class="subscript" />
    </section>

    <!-- 目录 -->
    <section class="catalogue" v-if="Info" @click="searchGrabble(Sol,Info.title)">
      <span>目录</span>
      <span v-if="Info.isSerial" :class="{ isSerial: Info.isSerial }">{{ timeMaker(Info.updated) }}</span>
      <span v-else>已完结</span>
      <span>
        {{this.Info.lastChapter}}
        <van-icon name="arrow" size="20" />
      </span>
    </section>
    <!-- 短评 -->

    <ul class="discuss" v-if="RemarkD">
      <h6>热门短评</h6>

      <li v-for="(item, index) in RemarkD" :key="index">
        <p class="name">{{item.author.nickname}}</p>

        <p class="content">{{item.content}}</p>
        <p class="content" v-if="item.updated">{{timeMaker(item.updated)}}</p>
      </li>
    </ul>

    <!-- 热门评论 -->
    <ul class="discuss">
      <h6>热门书评</h6>
      <li v-for="(item, index) in RemarkL" :key="index">
        <p class="name">{{item.author.nickname}}</p>
        <p class="titles">{{item.title}}</p>
        <p class="content">{{item.content}}</p>
      </li>
    </ul>

    <Footer />
  </div>
</template>

<script>
import HomeHead from "@/components/HomeHead.vue";
import Footer from "@/components/Footer.vue";

export default {
  name: "SearchDetails",
  components: {
    HomeHead,
    Footer
  },
  data() {
    return {
      iffull: true,
      Info: null,
      RemarkD: null,
      RemarkDall: null,
      RemarkL: null,
      RemarkLall: null,

      Code: null,
      Sol: null
    };
  },
  methods: {
    translatePlayCount(n) {
      if (n > 9999) {
        return (n / 10000).toFixed(0) + "万";
      } else {
        return n;
      }
    },
    moods(n) {
      if (n > 9999) {
        return (n / 10000).toFixed(1) + "万";
      } else {
        return n;
      }
    },
    timeMaker(t) {
      var date = new Date(t); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var now = new Date();

      var Y = date.getFullYear();
      var M =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var D = date.getDate();
      var h = date.getHours();
      var m = date.getMinutes();
      var s = date.getSeconds();
      var NY = now.getFullYear();
      var NM =
        now.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var ND = now.getDate();
      var Nh = now.getHours();
      var Nm = now.getMinutes();
      var Ns = now.getSeconds();

      if (NY - Y > 0) {
        return NY - Y + "年前更新";
      } else if (NM - M > 0) {
        return NM - M + "月前更新";
      } else if (ND - D > 0) {
        return ND - D + "天前更新";
      } else if (Nh - h > 0) {
        return Nh - h + "时前更新";
      } else if (Nm - m > 0) {
        return Nm - m + "分前更新";
      } else if (Ns - s > 0) {
        return Ns - s + "秒前更新";
      }
      // this.bookDetails.updated = Y + M + D + h + m + s;
    },
    // 图片解码
    changeUrl(Url) {
      if (Url) {
        return unescape(Url.slice(7));
      }
    },

    // 书籍源内容
    getInfo(id) {
      // 前面是用路由传参，要加上this.$route接收。
      this.axios.get("http://novel.kele8.cn/book-info/" + id).then(response => {
        window.console.log("书籍源内容", response);
        this.Info = response.data;
        // window.console.log("^_^",this.Info)
      });
    },
    // 短评论
    getRemarkD(id) {
      this.axios
        .get("http://novel.kele8.cn/book/short-reviews?book=" + id)
        .then(response => {
          window.console.log("短评论", response);
          this.RemarkDall = response.data.docs;

          this.RemarkD = this.RemarkDall.slice(0, 5);
        });
    },
    // 评论
    getRemarkL(id) {
      this.axios
        .get("http://novel.kele8.cn/book/reviews?book=" + id)
        .then(response => {
          window.console.log("评论", response);
          this.RemarkLall = response.data.reviews;
          this.RemarkL = this.RemarkLall.slice(0, 5);
        });
    },

    // 书籍源
    getCode(id) {
      this.axios
        .get("http://novel.kele8.cn/book-sources?view=summary&book=" + id)
        .then(response => {
          window.console.log("书籍源", response);
          this.Code = response.data;
          this.getSol(response.data[0]._id);
        });
    },

    // 章节列表
    getSol(id) {
      this.axios
        .get("http://novel.kele8.cn/book-chapters/" + id)
        .then(response => {
          window.console.log("章节列表", response);
          this.Sol = response.data;
        });
    },
    // 点击跳转章节列表
    searchGrabble(Sol,title) {
      this.$router.push({
        path: "/searchGrabble",
        query: { id: Sol._id ,title:title}
      });
    }
  },
  created() {},
  beforeRouteEnter(to, from, next) {
    // ...
    next(vm => {
      vm.getInfo(to.query.id);
      vm.getRemarkD(to.query.id);
      vm.getRemarkL(to.query.id);
      vm.getCode(to.query.id);
    });
  }
};
</script>

